<template>
  <v-card style="background-color: transparent" elevation="0" :disabled="loading" :loading="loading">
    <v-row>
      <v-col class="py-2" cols="12">


        <v-dialog v-model="dialog" width="500" :persistent="loadingDialog">
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" class="dash-btns dash-btns--blue" height="32" dark>
              <v-icon small class="ml-1">mdi-plus</v-icon>
              اسلاید جدید
            </v-btn>
          </template>
          <v-card v-if="dialog">
            <a-card-title>ایجاد کاربر</a-card-title>

            <v-card-text class="pt-6">

              <v-form ref="form">
                <v-row class="pa-0 py-6">
                  <v-col class="py-0" cols="12" md="6">
                    <v-text-field type="text" outlined color="txtf" dense :disabled="loadingDialog" v-model="desc"
                      :loading="loadingDialog" label="ریزنوشته" background-color="rgba(0,0,0,.05)">
                    </v-text-field>
                  </v-col>
                  <v-col class="py-0" cols="12" md="6">
                    <v-text-field type="text" outlined color="txtf" dense :disabled="loadingDialog" v-model="title"
                      :loading="loadingDialog" label="درشت نوشته" background-color="rgba(0,0,0,.05)">
                    </v-text-field>
                  </v-col>
                  <v-col class="py-0" cols="12" md="12">
                    <v-text-field type="text" outlined color="txtf" dense :disabled="loadingDialog" v-model="address"
                      :loading="loadingDialog" label="آدرس اینترنتی" background-color="rgba(0,0,0,.05)">
                    </v-text-field>
                  </v-col>

                  <v-col class="py-0" cols="12" md="12">
                    <v-file-input outlined color="txtf" dense :disabled="loadingDialog" v-model="banner"
                      :loading="loadingDialog" label="آپلود عکس" background-color="rgba(0,0,0,.05)">
                    </v-file-input>
                  </v-col>

                </v-row>
              </v-form>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="#222e3c" dark :disabled="loadingDialog" :loading="loadingDialog" @click="addSlide">
                ایجاد کردن
              </v-btn>
            </v-card-actions>

          </v-card>

        </v-dialog>


      </v-col>
      <v-col v-for="(item, index) in slides" :key="index + 'slideItemsx'" cols="12" md="4">
        <BlockItem>
          <span slot="title" v-if="index % 3 === 0">اسلایدر {{ index + 1 }}</span>
          <template slot="icon" v-if="index % 3 === 0">mdi-page-layout-sidebar-right</template>
          <span slot="title" v-if="index % 3 === 1">اسلایدر {{ index + 1 }}</span>
          <template slot="icon" v-if="index % 3 === 1">mdi-page-layout-body</template>
          <span slot="title" v-if="index % 3 === 2">اسلایدر {{ index + 1 }}</span>
          <template slot="icon" v-if="index % 3 === 2">mdi-page-layout-sidebar-left</template>
          <SlideItem @deleteFn="fetchSlides()" :item="item"></SlideItem>
        </BlockItem>
      </v-col>
    </v-row>
    <ImgViewDialog />
    <template v-slot:progress>
      <GDLoading />
    </template>
  </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import ImgViewDialog from "@/components/Misc/ImgViewDialog";
import axios from "axios";
import SlideItem from "@/components/Dashboard/MainSliders/SlideItem";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";
export default {
  name: "MainSliders",
  created() {
    this.fetchSlides();
  },
  components: {
    ACardTitle,
    GDLoading,
    SlideItem,
    ImgViewDialog,
    BlockItem
  },
  data() {
    return {
      loading: false,
      slides: [],
      desc: null,
      title: null,
      address: null,
      banner: null,
      dialog: false,
      loadingDialog: false
    }
  },
  methods: {
    fetchSlides() {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/sliders').then(response => {
        this.slides = response.data.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    },
    addSlide() {
      if (this.banner.size > 10485760) {
        this.notify('حجم فایل بیشتر از 10 م.گ', 'error')
        return
      }
      this.loadingDialog = true;
      let formData = new FormData();
      formData.append('banner', this.banner);
      formData.append('title', this.title);
      formData.append('desc', this.desc);
      formData.append('address', this.address);

      this.title = null;
      this.desc = null;
      this.address = null;

      axios.post(this.$store.state.host + '/website/api/v1/website/create-slider', formData, {
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
          'Content-Type': 'multipart/form-data',
          Accept: "*/*",
        }
      }).then(() => {
        this.notify('اضافه شد', 'success');
        this.fetchSlides();
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.dialog = false;
        this.loadingDialog = false;
      })
    }

  }
}
</script>

