<template>
  <div>
    <v-card elevation="0">
      <v-card :disabled="loadingUpload" :loading="loadingUpload" elevation="0" class="upload-div">
        <v-img @click="showImage(data.banner)" style="cursor: pointer" :lazy-src="data.banner" max-width="180"
          height="120" :src="data.banner">
        </v-img>
        <div class="upload-slider">
          <a>
            <label style="cursor: pointer">
              <input type="file" ref="file" @change="uploadFile()" style="display: none;">
              <v-icon color="info">mdi-cloud-upload</v-icon>
              آپلود تصویر
            </label>

          </a>
          <br>
          <a @click="showImage(item.banner)"><v-icon color="info">mdi-eye</v-icon>
            نمایش تصویر</a>
        </div>
      </v-card>
      <v-text-field outlined dense color="txtf" class="mt-5" v-model="data.desc" label="ریز نوشته">

      </v-text-field>
      <v-text-field outlined dense color="txtf" v-model="data.title" label="درشت نوشته">

      </v-text-field>
      <v-text-field outlined dense color="txtf" class="email-field" v-model="data.address" label="آدرس اینترنتی">

      </v-text-field>

      <div class="d-flex justify-space-between">
        <v-btn v-if="$store.state.permissions && $store.state.permissions['change-slider']" :loading="loading"
          :disabled="loading" @click="updateSlider()" height="32" class="dash-btns dash-btns--blue mt-2" dark>ذخیره
          تغییرات</v-btn>

        <v-btn @click="deleteSlide(item.id)" icon>
          <v-icon color="error">mdi-delete</v-icon>
        </v-btn>
      </div>

    </v-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "SlideItem",
  props: ["item"],
  data() {
    return {
      loadingUpload: false,
      loading: false,
      data: {
        desc: this.item.desc,
        title: this.item.title,
        address: this.item.address,
        banner: this.item.banner
      }
    }
  },
  methods: {
    deleteSlide(id) {
      this.loading = true;
      axios.delete(this.$store.state.host + '/website/api/v1/website/delete-slider/' + id, {
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(() => {
        this.notify('حذف شد', 'warning');
        this.$emit('deleteFn')
      }).finally(() => {
        this.loading = false;
      })
    },
    uploadFile() {

      this.file = this.$refs.file.files[0];
      if (this.file.size > 2097152) {
        this.notify('حجم عکس بیشتر از 2 م.گ', 'error')
        return
      }
      if ((this.file.type !== 'image/jpeg') && (this.file.type !== 'image/jpg') && (this.file.type !== 'image/png')) {
        this.notify('فرمت غیر قابل پشتیبانی', 'error')
        return
      }
      this.loadingUpload = true;
      let formData = new FormData();
      formData.append('banner', this.file);
      const headers = {
        'Content-Type': 'multipart/form-data',
        Accept: "*/*",
        Authorization: `Bearer ${this.$store.state.userId}`,
      };
      axios.patch(this.$store.state.host + '/website/api/v1/website/change-slider/' + this.item.id,
        formData
        , { headers }).then((res) => {
          if (res.data.result) {
            this.data.banner = res.data.data.banner;
          }
        }).finally(() => {
          this.loadingUpload = false;
        });
    },
    showImage(url) {
      this.$store.state.imgUrl = url;
      this.$store.state.imgDialog = true;
    },
    updateSlider() {
      this.loading = true;
      const headers = {
        Accept: "application/json",
        Authorization: `Bearer ${this.$store.state.userId}`,
      };
      axios.patch(this.$store.state.host + '/website/api/v1/website/change-slider/' + this.item.id,
        {
          title: this.data.title,
          address: this.data.address,
          desc: this.data.desc,
        }
        , { headers }).then((res) => {
          if (res.data.result) {
            //this.item.banner = res.data.data.banner;
            this.notify('اسلاید بروز شد.', 'info')
          }
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.loading = false;
        });
    }
  }
}
</script>

