.upload-div {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.upload-slider {
  color: #615e5e;
  font-size: 14px;
  line-height: 40px;

  a {
    color: #615e5e;

    &:hover {
      color: #D7926B;
    }
  }
}

.email-field::v-deep input {
  text-align: left;
}
</style>
